import type { ReactElement } from 'react';
import { useFormik, type FormikHelpers } from 'formik';
import { useIntl } from 'react-intl';
import LocalizedTextField from '@commercetools-uikit/localized-text-field';
import TextField from '@commercetools-uikit/text-field';
import Spacings from '@commercetools-uikit/spacings';
import SelectField from '@commercetools-uikit/select-field';
import type { TFormValues } from '../../types';
import { CHANNEL_ROLES } from './constants';
import validate from './validate';
import messages from './messages';
import { TStateRole, TReviewCustomState } from '../../types/generated/ctp';

type TChannelRole = keyof typeof CHANNEL_ROLES;
type Formik = ReturnType<typeof useFormik>;
type FormProps = {
  formElements: ReactElement;
  values: Formik['values'];
  isDirty: Formik['dirty'];
  isSubmitting: Formik['isSubmitting'];
  submitForm: Formik['handleSubmit'];
  handleReset: Formik['handleReset'];
};

type TReviewFormValues = {
  key: string;
  rating: string;
  state: TReviewCustomState;
  version: string;
};

type TReviewDetailsFormProps = {
  onSubmit: (
    values: TFormValues,
    formikHelpers: FormikHelpers<TFormValues>
  ) => void | Promise<unknown>;
  initialValues: TReviewFormValues;
  isReadOnly: boolean;
  dataLocale: string;
  children: (formProps: FormProps) => JSX.Element;
};

const getRoleOptions = Object.keys(CHANNEL_ROLES).map((key) => ({
  label: CHANNEL_ROLES[key as TChannelRole],
  value: CHANNEL_ROLES[key as TChannelRole],
}));

const ReviewDetailsForm = (props: TReviewDetailsFormProps) => {
  const intl = useIntl();
  const formik = useFormik<TFormValues>({
    initialValues: props.initialValues,
    onSubmit: props.onSubmit,
    validate,
    enableReinitialize: true,
  });

  const formElements = (
    <Spacings.Stack scale="l">
      <TextField
        name="key"
        title={intl.formatMessage(messages.channelKeyLabel)}
        value={formik.values.key}
        errors={TextField.toFieldErrors<TFormValues>(formik.errors).key}
        touched={formik.touched.key}
        // onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        isReadOnly={true}
        renderError={(errorKey) => {
          if (errorKey === 'duplicate') {
            return intl.formatMessage(messages.duplicateKey);
          }
          return null;
        }}
        isRequired
        horizontalConstraint={13}
      />
      <TextField
        name="version"
        title={intl.formatMessage(messages.channelVersion)}
        value={formik.values.version}
        errors={TextField.toFieldErrors<TFormValues>(formik.errors).version}
        touched={formik.touched.version}
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        isReadOnly={true}
        renderError={(errorKey) => {
          if (errorKey === 'duplicate') {
            return intl.formatMessage(messages.duplicateKey);
          }
          return null;
        }}
        isRequired
        horizontalConstraint={13}
      />
      <TextField
        name="rating"
        title={intl.formatMessage(messages.channelRatingLabel)}
        value={formik.values.rating}
        errors={TextField.toFieldErrors<TFormValues>(formik.errors).rating}
        touched={formik.touched.rating}
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        isReadOnly={true}
        renderError={(errorKey) => {
          if (errorKey === 'duplicate') {
            return intl.formatMessage(messages.duplicateKey);
          }
          return null;
        }}
        horizontalConstraint={13}
      />
      <SelectField
        name="state"
        title={intl.formatMessage(messages.channelRolesLabel)}
        value={formik.values.state}
        errors={SelectField.toFieldErrors<TFormValues>(formik.errors).state}
        touched={formik.touched.state}
        onChange={formik.handleChange}
        options={getRoleOptions}
        isReadOnly={props.isReadOnly}
        horizontalConstraint={13}
      />
    </Spacings.Stack>
  );

  return props.children({
    formElements,
    values: formik.values,
    isDirty: formik.dirty,
    isSubmitting: formik.isSubmitting,
    submitForm: formik.handleSubmit,
    handleReset: formik.handleReset,
  });
};
ReviewDetailsForm.displayName = 'ReviewDetailsForm';

export default ReviewDetailsForm;
